html,body
{
  width: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  color: #a3b3c3;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(4px + 2vmin);
  min-height: 100vh;
  vertical-align: top;
}

.App-link {
  color: #09d3ac;
}

.Button {
  background-color: #4CAF50;
  border: none;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  margin: 4px 2px;
  cursor: pointer;
}

.UploadButton {
  display: inline-block;
}
.UploadButtonHide {
  display:none;
}

.FileContent
{
  display: block;
  font-size: 14px;
  max-width : 90%;
  padding: 10px 10px;
  text-align: left;
  white-space: pre-line;
}

.FileDetails
{
  font-size: 14px;
  text-align: left;
  vertical-align: text-top;
  white-space: pre-line;
  margin-top: 5px;
  margin-left: 5px;
  max-width: 350px;
}

.div-table {
  display: block;
  margin-top: 10px;
  width: 600px;          
}

.div-table-col {
  display: table-cell;
  float: left;
}

.div-table-col-right {
  float: right;
}